:root
{
    --card-graphics-padding: 0.25rem;
    --back-color: rgb(133, 169, 190);
    --back-pattern-color: rgba(255, 255, 255, 0.2);
    --back-pattern-color-2: rgba(0, 0, 0, 0.1);
}

.card
{
    position: relative;
    background: rgb(207, 189, 176);
    border-radius: 0.5rem;
    width: 8rem;
    height: 12rem;
    overflow: hidden;
    cursor: grab;
    border: 1px solid rgba(0, 0, 0, 0.8);
    box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.1);
}

.card.dragging
{
    z-index: 100;
    cursor: grabbing;
    box-shadow: 2px 2px 3px 3px rgba(0, 0, 0, 0.08);
}

.card > .back,
.card > .front
{
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 0.25rem;
}

.card > .back
{
    background-color: var(--back-color);
    background-image:
        repeating-radial-gradient(
            circle at 0 0,
            transparent 0,
            var(--back-color) 10px
        ),
        repeating-linear-gradient(
            var(--back-pattern-color-2),
            var(--back-pattern-color)
        );
}

.card > .front
{
    top: var(--card-graphics-padding);
    left: var(--card-graphics-padding);
    width: calc(100% - calc(var(--card-graphics-padding) * 2));
    height: calc(100% - calc(var(--card-graphics-padding) * 2));
    background-color: rgba(0, 0, 0, 0.3);
}