.pile
{
    position: relative;
    background: rgba(167, 148, 134, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.25);
    padding: 0.25em;
    border-radius: 0.5em;
    width: 10em;
    height: 14em;
    transition: border-color 0.25s, transform 0.25s;
    /* overflow: hidden; */
}

.pile.active
{
    border-color: rgba(255, 255, 255, 0.5);
    /* transform: scale(1.025); */
}